.popup__close {
  position: absolute;
  width: 32px;
  height: 32px;
  /*left: 470px;
    bottom: 190px;*/
  top: -40px;
  right: -40px;
  opacity: 1;
  background: transparent url(../../../images/close__icon.png) center no-repeat;
  border: none;
  cursor: pointer;
}

.popup__close:hover {
  opacity: 0.6;
}

@media screen and (max-width: 768px) {
  .popup__close {
    width: 20px;
    height: 20px;
    top: -36px;
    right: 0;
  }
}
