.popup {
  display: flex;
  position: fixed;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.5s, opacity 0.5s ease-in;
  background-color: rgba(0, 0, 0, 0.9);
}

.popup_opened {
  opacity: 1;
  visibility: visible;
}
